html {
    font-family: 'helvetica neue', helvetica, arial, sans-serif;
    background-color: #000000;
}

thead th, tfoot th {
    font-family: 'Rock Salt', cursive;
}

#cal-table {

    width: 99%;
    border-collapse: collapse;
    font-size: small;
    table-layout: fixed;
}

th, td {
    vertical-align: top;
    padding-bottom: 10px;
}

#header-bar {
    width: 99%;
    border-collapse: collapse;
    margin: auto;
    vertical-align: bottom;
}

#header-h1 {
  vertical-align: middle;
  text-shadow: 3px 3px 8px #D5E5F2;
  color: white;
}

#logo-img {
  vertical-align: middle;
  padding-top: 10px;
  padding-bottom: 10px;
}

tbody td {
  text-align: left;
}

tfoot th {
  text-align: right;
}

tbody tr:nth-child(odd) {
  background-color: #ffffff;
}

tbody tr:nth-child(even) {
  background-color: #dddddd;
}

.homeworkButton {
    border: double;
    border-radius: 8px;
    background-color: purple;
    color: white;
    font-size: medium !important;
}

.container {
  color: white;
    border: solid 8px;
    border-radius: 15px;
    border-color: #32628C;
  background-color: #000000;
    padding-bottom: 10px;
    margin: auto;
    margin-bottom: 15px;
}

#cal-link, #docs-link, #contact-link {
  letter-spacing: 3px;
  width: 99%;
  background-color: #000000;
  color: white;
  border-top-left-radius: 8px;
   border-top-right-radius: 8px;
  border-top-style: solid;
  border-bottom-style: none;
  border-left-style: solid;
  border-right-style: solid;
  border-top-color: #D5E5F2;
  border-right-color: #D5E5F2;
  border-width: 3px;
  padding-top: 8px;
  padding-bottom: 8px;
}

table {
  width: 100%;
}